body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: url(/personal-page/static/media/background.f4184ae8.jpg);
  height: 100%;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.body, html {
  height: 100vh;
  min-height: 100vh;
  margin: 0;
}

.textSpot {
  background: rgba(90, 128, 122, .8);
  color: white;
  margin: 3%;
  border-radius: 20px;
  height: 100%;
}

.headerEmail {
  margin: 3%;
  padding-top: 2%;
}

.App-header {
  background-color: #3f5a56;
  /* min-height: 10%; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a:link {
  color: rgb(255, 128, 122);
}

a:visited {
  color: rgb(255, 128, 122);
}

.headshot {
  height: 400px;
  width: auto;
  margin-bottom: 2%;
}

.bottomBox {
  padding-bottom: 2%;
}

.screenshots {
  height: 300px;
  width: auto;
}

.footer {
  padding-bottom: 2%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

